@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/Inter-Regular.woff2") format("woff2"), url("/fonts/Inter-Regular.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 400;
  src: url("/fonts/Inter-Italic.woff2") format("woff2"), url("/fonts/Inter-Italic.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/Inter-Bold.woff2") format("woff2"), url("/fonts/Inter-Bold.woff") format("woff"); }

@font-face {
  font-family: 'Inter';
  font-style: italic;
  font-weight: 700;
  src: url("/fonts/Inter-BoldItalic.woff2") format("woff2"), url("/fonts/Inter-BoldItalic.woff") format("woff"); }

.header {
  display: flex;
  justify-content: space-between;
  margin: 0.9375rem;
  align-items: center; }

.headline {
  font-family: "Inter";
  font-size: 1.0625rem;
  margin: 0; }

.url {
  font-family: "Inter";
  font-size: 0.9375rem;
  color: #4360df;
  text-decoration: none; }

.categories {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  margin: 0 0.625rem 1.875rem 0.625rem; }
  @media (min-width: 830px) {
    .categories {
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      grid-template-rows: unset; } }

.category {
  background: #fff;
  font-family: "Inter";
  font-size: 0.8125rem;
  line-height: 1rem;
  color: #000;
  border: none;
  border-radius: 0.75rem;
  padding: 0.75rem;
  margin: 0.25rem;
  cursor: pointer;
  text-align: left; }
  .category p {
    margin-bottom: 0; }

.EXCHANGES {
  background: rgba(136, 122, 249, 0.15); }

.MARKETPLACES {
  background: rgba(254, 143, 89, 0.15); }

.OTHER {
  background: rgba(255, 202, 15, 0.15); }

.MEDIA {
  background: rgba(255, 202, 15, 0.15); }

.GAMES {
  background: rgba(211, 126, 244, 0.15); }

.COLLECTIBLES {
  background: rgba(81, 208, 240, 0.15); }

.SOCIAL_NETWORKS {
  background: rgba(124, 218, 0, 0.15); }

.UTILITIES {
  background: rgba(250, 101, 101, 0.15); }
